import React from 'react'
import './Sidebar.css'
import SidebarRow from './SidebarRow'
import HomeIcon from '@mui/icons-material/Home';
import ExploreOutlinedIcon from '@mui/icons-material/ExploreOutlined';
import SubscriptionsOutlinedIcon from '@mui/icons-material/SubscriptionsOutlined';
import AirplayIcon from '@mui/icons-material/Airplay';
import VideoLibraryIcon from '@mui/icons-material/VideoLibrary';
import RestoreIcon from '@mui/icons-material/Restore';
import OndemandVideoOutlinedIcon from '@mui/icons-material/OndemandVideoOutlined';
import AccessTimeOutlinedIcon from '@mui/icons-material/AccessTimeOutlined';
import ThumbUpOutlinedIcon from '@mui/icons-material/ThumbUpOutlined';

function Sidebar(){
    return(
        <div className="sidebar">
            <SidebarRow Icon={HomeIcon} title="Trang ch"/>
            <SidebarRow Icon={ExploreOutlinedIcon} title="Khm ph"/>
            <SidebarRow Icon={AirplayIcon} title="Shorts"/>
            <SidebarRow Icon={SubscriptionsOutlinedIcon} title="Knh ng k"/>
            <hr></hr>
            <SidebarRow Icon={VideoLibraryIcon} title="Th vin"/>
            <SidebarRow Icon={RestoreIcon} title="Video  xem"/>
            <SidebarRow Icon={OndemandVideoOutlinedIcon} title="Video ca bn"/>
            <SidebarRow Icon={AccessTimeOutlinedIcon} title="Xem sau"/>
            <SidebarRow Icon={ThumbUpOutlinedIcon} title="Video  thch"/>
        </div>
    )
}

export default Sidebar